/* üåô Full screen layout (clean + stable) */
.login-container {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom, #07204a, #001124);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}


/* ‚ùÑÔ∏è Real falling snowflakes */
.snowflake {
  position: absolute;
  top: -10px;
  font-size: 18px;
  color: white;
  opacity: 0.8;
  animation: fall 7s linear infinite;
  pointer-events: none;
}

@keyframes fall {
  0% { transform: translateY(-10vh) rotate(0deg); }
  100% { transform: translateY(110vh) rotate(360deg); }
}


/* üéÅ Login Card */
.login-card {
  background: rgba(255, 255, 255, 0.12);
  padding: 35px;
  border-radius: 18px;
  backdrop-filter: blur(14px);
  box-shadow: 
    0 0 25px rgba(0, 255, 180, 0.25),
    0 0 55px rgba(0, 120, 255, 0.25);
  width: 90%;
  max-width: 350px;
  text-align: center;
  z-index: 10; /* stays above snow */
}


/* üéÑ Title */
.login-title {
  color: #ff4eb8;
  font-size: 30px;
  font-weight: 800;
  margin-bottom: 20px;
  text-shadow:
    0 0 8px #ff4eb8,
    0 0 15px #00ffbb,
    0 0 25px #007bff;
}


/* ‚ú® Inputs */
.login-input {
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  border-radius: 10px;
  border: none;
  font-size: 16px;
  outline: none;
}


/* üéÖ Magic Button */
.login-btn {
  margin-top: 16px;
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  font-weight: 700;
  background: linear-gradient(to right, #ff3131, #29d700);
  color: white;
  cursor: pointer;
  box-shadow:
    0 0 10px red,
    0 0 18px green;
  transition: 0.2s;
}

.login-btn:hover {
  transform: scale(1.05);
}


/* Small text */
.login-small {
  color: white;
  margin-top: 18px;
  font-size: 14px;
}

/* Social icons */
.login-social {
  font-size: 28px;
  margin-top: 8px;
  color: #fff;
}
