/* ================================
   üåô NIGHTTIME BASE SCENE BACKGROUND
   ================================ */
.sceneContainer {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;

  background: linear-gradient(180deg, #001633, #000a1a, #001633);
}

/* ================================
   ‚õÑ FROSTY SCENE
   ================================ */
.frosty .sceneImage {
  width: 640px;
  height: auto;
  animation: float 3s ease-in-out infinite;
  z-index: 3;
}

/* ================================
   üéµ SILENT NIGHT SCENE
   ================================ */
.silentnight .sceneImage {
  width: 750px;   /* Bigger landscape village */
  height: auto;
  animation: float 3s ease-in-out infinite;
  z-index: 3;
}

/* Floating animation */
@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-14px); }
  100% { transform: translateY(0); }
}

/* ================================
   üéÑ BACK BUTTON
   ================================ */
.backBtn {
  position: absolute;
  top: 20px;
  left: 20px;

  padding: 10px 22px;
  font-size: 18px;
  border-radius: 12px;
  border: none;

  background: linear-gradient(135deg, #44c7ff, #8af5ff);
  color: #001527;
  font-weight: 700;

  cursor: pointer;
  z-index: 15;
  box-shadow: 0 0 14px rgba(160, 240, 255, 0.8);
  transition: 0.18s ease-in-out;
}

.backBtn:hover {
  transform: scale(1.07);
  box-shadow: 0 0 22px rgba(200, 255, 255, 1);
}

/* ================================
   ‚ùÑÔ∏è FALLING SNOW
   ================================ */
.snowContainer {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 10;
}

.snowflake {
  position: absolute;
  top: -20px;
  color: white;
  opacity: 0.92;

  animation-name: snowfall;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  left: calc(var(--x) * 1%);
  font-size: calc(8px + var(--size) * 1px);
  animation-duration: calc(3.5s + var(--speed) * 2s);
  animation-delay: calc(var(--delay) * 1s);
}

@keyframes snowfall {
  0% {
    transform: translateY(-10vh) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translateY(110vh) rotate(360deg);
    opacity: 0;
  }
}

/* ================================
   üì± MOBILE
   ================================ */
@media (max-width: 480px) {
  .frosty .sceneImage,
  .silentnight .sceneImage {
    width: 250px;
  }

  .backBtn {
    font-size: 16px;
    padding: 8px 16px;
  }
}
