/* üåå Matching login background */
.playlist-wrapper {
  min-height: 100vh;
  width: 100%;
  background: linear-gradient(to bottom, #0d1b3d, #001124);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

/* ‚ùÑ Snow container */
.playlist-snow {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

/* ‚ùÑ Individual snowflakes */
.snowflake-small {
  position: absolute;
  top: -10vh;
  color: #dff6ff;
  opacity: 0.9;
  animation: snowFall 7s linear infinite;
}

/* ‚ùÑ Smooth falling animation */
@keyframes snowFall {
  0% { transform: translateY(-10vh) rotate(0deg); }
  100% { transform: translateY(110vh) rotate(360deg); }
}

/* üéÑ Title bar */
.playlist-title {
  background: linear-gradient(90deg, #28c0ff, #b0f3ff);
  padding: 15px 40px;
  border-radius: 14px;
  font-size: 30px;
  font-weight: 700;
  color: white;
  text-shadow: 0 0 12px rgba(255,255,255,0.8);
  margin-top: 20px;
  margin-bottom: 40px;
  animation: lightPulse 4s ease-in-out infinite;
}

@keyframes lightPulse {
  0% { filter: brightness(1); }
  50% { filter: brightness(1.25); }
  100% { filter: brightness(1); }
}

/* üéµ Song list */
.song-list {
  width: 100%;
  max-width: 650px;
}

/* üéß Song card */
.song-row {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(12px);
  border-radius: 22px;
  padding: 25px 30px;
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 0.2s ease;
  box-shadow: 0 0 25px rgba(0, 255, 200, 0.28);
}

.song-row:hover {
  transform: scale(1.03);
  box-shadow: 0 0 35px rgba(0, 255, 200, 0.45);
}

/* Thumbnail circle */
.song-thumb {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background: linear-gradient(135deg, #52dfff, #d7faff);
  margin-right: 25px;
  flex-shrink: 0;
  box-shadow: 0 0 15px rgba(150, 220, 255, 0.7);
}

/* Song info */
.song-info {
  flex: 1;
  color: white;
  min-width: 0;
}

.song-name {
  font-size: 22px;
  font-weight: 600;
  margin: 0;
}

.song-artist {
  color: #b9daf5;
  margin-top: 5px;
}

.song-album {
  color: #8cd3ff;
  font-size: 14px;
  margin-top: 6px;
}

/* Play / Gift icons */
.song-actions span {
  font-size: 24px;
  cursor: pointer;
  transition: 0.18s;
}

.song-actions span:hover {
  transform: scale(1.25);
}

/* üì± Mobile tweaks */
@media (max-width: 480px) {
  .playlist-title {
    font-size: 22px;
    padding: 10px 25px;
  }

  .song-row {
    padding: 20px;
    flex-direction: column;
    text-align: center;
    gap: 18px;
  }

  .song-thumb {
    margin-right: 0;
  }
}
